<template>
  <div class="create-listing-modal">
    <div class="create-listing-modal__box">
      <h3 class="create-listing-modal__title">Create NFT Listing</h3>
      
      <div v-if="isLoading" class="create-listing-modal__loader-container">
        <div class="create-listing-modal__loader"></div>
      </div>
      
      <div v-else-if="userNfts.length === 0" class="create-listing-modal__empty">
        <p class="create-listing-modal__empty-title">You don't have any NFTs to list.</p>
        <p class="create-listing-modal__empty-subtitle">Mint or acquire NFTs first before creating a listing.</p>
      </div>
      
      <div v-else>
        <!-- Выбор NFT -->
        <div class="create-listing-modal__section">
          <label class="create-listing-modal__label">Select NFT</label>
          <div class="create-listing-modal__nft-grid">
            <div 
              v-for="nft in userNfts" 
              :key="nft.mintAddress"
              @click="selectNft(nft)"
              class="create-listing-modal__nft-item"
              :class="{'create-listing-modal__nft-item--selected': selectedNft && selectedNft.mintAddress === nft.mintAddress}"
            >
              <div class="create-listing-modal__nft-image-container">
                <img 
                  v-if="nft.imageUrl" 
                  :src="nft.imageUrl" 
                  :alt="nft.name" 
                  class="create-listing-modal__nft-image"
                />
                <div v-else class="create-listing-modal__nft-placeholder">
                  <i class="fas fa-image create-listing-modal__nft-placeholder-icon"></i>
                </div>
              </div>
              <div class="create-listing-modal__nft-info">
                <p class="create-listing-modal__nft-name">{{ nft.name || `NFT #${nft.mintAddress.substring(0, 8)}` }}</p>
              </div>
            </div>
          </div>
        </div>
        
        <!-- Форма создания листинга -->
        <div class="create-listing-modal__form">
          <div class="create-listing-modal__form-group">
            <label class="create-listing-modal__form-label">
              <span class="create-listing-modal__form-label-text">Initial Price (SOL)</span>
            </label>
            <input 
              v-model="form.initialPrice" 
              type="number" 
              step="0.01" 
              min="0.01" 
              placeholder="Enter initial price" 
              class="create-listing-modal__form-input" 
              required
            />
            <span class="create-listing-modal__form-help">Minimum price for the auction</span>
          </div>
          
          <div class="create-listing-modal__form-group">
            <label class="create-listing-modal__form-label">
              <span class="create-listing-modal__form-label-text">Instant Buy Price (SOL, optional)</span>
            </label>
            <input 
              v-model="form.instantPrice" 
              type="number" 
              step="0.01" 
              min="0" 
              placeholder="Enter instant buy price (optional)" 
              class="create-listing-modal__form-input"
            />
            <span class="create-listing-modal__form-help">Price for immediate purchase</span>
          </div>
          
          <div class="create-listing-modal__form-group">
            <label class="create-listing-modal__form-label">
              <span class="create-listing-modal__form-label-text">End Date</span>
            </label>
            <input 
              v-model="form.endDate" 
              type="datetime-local" 
              class="create-listing-modal__form-input" 
              required
              :min="minEndDate"
            />
            <span class="create-listing-modal__form-help">When the auction will end</span>
          </div>
          
          <div class="create-listing-modal__fee-summary">
            <h4 class="create-listing-modal__fee-title">Fees</h4>
            <div class="create-listing-modal__fee-row">
              <span>Marketplace Fee (5%)</span>
              <span>{{ calculateFee() }} SOL</span>
            </div>
            <div class="create-listing-modal__fee-row create-listing-modal__fee-row--total">
              <span>You will receive</span>
              <span>{{ calculateReceived() }} SOL</span>
            </div>
          </div>
          
          <div class="create-listing-modal__button-group">
            <button 
              type="button"
              @click="$emit('close')" 
              class="create-listing-modal__button create-listing-modal__button--cancel"
            >
              Cancel
            </button>
            <button 
              type="button"
              @click="createListingManually" 
              class="create-listing-modal__button create-listing-modal__button--submit"
              :disabled="!isFormValid || isSubmitting"
            >
              {{ isSubmitting ? 'Creating...' : 'Create Listing' }}
            </button>
          </div>
        </div>
      </div>
    </div>
    <div class="create-listing-modal__backdrop" @click="$emit('close')"></div>
  </div>
</template>

<script>
export default {
  name: 'CreateListingModal',
  props: {
    userNfts: {
      type: Array,
      required: true
    },
    isLoading: {
      type: Boolean,
      default: false
    },
    isSubmitting: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      selectedNft: null,
      form: {
        initialPrice: '',
        instantPrice: '',
        endDate: ''
      }
    };
  },
  computed: {
    minEndDate() {
      const now = new Date();
      now.setHours(now.getHours() + 1);
      return now.toISOString().slice(0, 16);
    },
    isFormValid() {
      return (
        this.selectedNft && 
        this.form.initialPrice > 0 && 
        this.form.endDate && 
        new Date(this.form.endDate) > new Date()
      );
    }
  },
  methods: {
    selectNft(nft) {
      console.log('Selecting NFT:', nft);
      if (!nft || !nft.mintAddress) {
        console.error('Invalid NFT selected:', nft);
        return;
      }
      this.selectedNft = nft;
      console.log('Selected NFT:', this.selectedNft);
    },
    calculateFee() {
      if (!this.form.initialPrice) return '0';
      const fee = parseFloat(this.form.initialPrice) * 0.05;
      return fee.toFixed(3);
    },
    calculateReceived() {
      if (!this.form.initialPrice) return '0';
      const total = parseFloat(this.form.initialPrice);
      const fee = total * 0.05;
      return (total - fee).toFixed(3);
    },
    createListingManually() {
      if (!this.isFormValid) return;
      
      console.log('Creating listing for NFT:', this.selectedNft);
      
      // Создаем копию объекта NFT, чтобы избежать проблем с Proxy и передать все свойства
      const nftData = {
        mintAddress: this.selectedNft.mintAddress,
        name: this.selectedNft.name || '',
        imageUrl: this.selectedNft.imageUrl || '',
        metadataUri: this.selectedNft.metadataUri || ''
      };
      
      console.log('Sending NFT data to parent:', nftData);
      
      this.$emit('submit', {
        nft: nftData,
        initialPrice: parseFloat(this.form.initialPrice),
        instantPrice: this.form.instantPrice ? parseFloat(this.form.instantPrice) : null,
        endDate: this.form.endDate
      });
    }
  }
};
</script>

