<template>
  <div class="nft-card">
    <!-- Изображение NFT -->
    <div class="nft-card__image-container">
      <img 
        v-if="listing.imageUrl" 
        :src="listing.imageUrl" 
        :alt="listing.name" 
        class="nft-card__image"
      />
      <div v-else class="nft-card__image-placeholder">
        <i class="fas fa-image nft-card__placeholder-icon"></i>
      </div>
      
      <!-- Статус листинга -->
      <div 
        v-if="listing.status !== 'active'" 
        class="nft-card__status"
        :class="{
          'nft-card__status--sold': listing.status === 'sold',
          'nft-card__status--cancelled': listing.status === 'cancelled',
          'nft-card__status--expired': listing.status === 'expired'
        }"
      >
        {{ listing.status }}
      </div>
    </div>
    
    <!-- Информация о листинге -->
    <div class="nft-card__content">
      <h3 class="nft-card__title">{{ listing.name || `NFT #${listing.mintAddress.substring(0, 8)}` }}</h3>
      <p class="nft-card__address">{{ shortenAddress(listing.mintAddress) }}</p>
      
      <div class="nft-card__seller" v-if="listing.userTag">
        <p class="nft-card__seller-label">Seller:</p>
        <p class="nft-card__seller-value">{{ listing.userTag }}</p>
      </div>
      
      <div class="nft-card__price-container">
        <div class="nft-card__price-block">
          <p class="nft-card__price-label">Price</p>
          <p class="nft-card__price-value">{{ formatPrice(listing.initialPrice) }} SOL</p>
        </div>
        <div v-if="listing.instantPrice" class="nft-card__price-block">
          <p class="nft-card__price-label">Buy Now</p>
          <p class="nft-card__price-value">{{ formatPrice(listing.instantPrice) }} SOL</p>
        </div>
      </div>
      
      <div class="nft-card__time-block">
        <p class="nft-card__time-label">Ends in</p>
        <p class="nft-card__time-value">{{ formatTimeRemaining(listing.endTime) }}</p>
      </div>
      
      <!-- Кнопки действий -->
      <div class="nft-card__actions">
        <button 
          @click="$emit('view', listing)" 
          class="nft-card__button nft-card__button--view"
        >
          View Details
        </button>
        <button 
          v-if="canBid"
          @click="$emit('bid', listing)"
          class="nft-card__button nft-card__button--bid"
        >
          Place Bid
        </button>
        <button 
          v-if="canCancel"
          @click="$emit('cancel', listing)"
          class="nft-card__button nft-card__button--cancel"
        >
          Cancel
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'NftListingCard',
  props: {
    listing: {
      type: Object,
      required: true
    },
    walletAddress: {
      type: String,
      default: ''
    },
    connected: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    canBid() {
      return this.connected && 
             this.listing.status === 'active' && 
             this.listing.sellerWallet !== this.walletAddress;
    },
    canCancel() {
      return this.connected && 
             this.listing.status === 'active' && 
             this.listing.sellerWallet === this.walletAddress;
    }
  },
  methods: {
    formatPrice(price) {
      return parseFloat(price).toFixed(3);
    },
    formatTimeRemaining(endTime) {
      const end = new Date(endTime);
      const now = new Date();
      
      if (end <= now) {
        return 'Ended';
      }
      
      const diff = end - now;
      const days = Math.floor(diff / (1000 * 60 * 60 * 24));
      const hours = Math.floor((diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));
      
      if (days > 0) {
        return `${days}d ${hours}h`;
      } else if (hours > 0) {
        return `${hours}h ${minutes}m`;
      } else {
        return `${minutes}m`;
      }
    },
    shortenAddress(address) {
      if (!address) return '';
      return `${address.substring(0, 4)}...${address.substring(address.length - 4)}`;
    }
  }
};
</script>

