<template>
  <div class="nft-market">
    <div class="nft-market__container">
      <h1 class="nft-market__title">NFT Marketplace</h1>
      
      <!-- Секция для создания листинга -->
      <div v-if="wallet && connected" class="nft-market__create-section">
        <h2 class="nft-market__section-title">Sell Your NFT</h2>
        <p class="nft-market__section-description">List your NFT for sale on our marketplace and earn SOL.</p>
        <button 
          @click="showCreateListingModal = true" 
          class="nft-market__create-button"
        >
          Create Listing
        </button>
      </div>
      
      <!-- Секция для отображения активных листингов -->
      <div class="nft-market__listings-section">
        <div class="nft-market__listings-header">
          <h2 class="nft-market__section-title">Active Listings</h2>
          <div class="nft-market__listings-controls">
            <button 
              @click="loadListings" 
              class="nft-market__refresh-button"
              :disabled="isLoading"
            >
              <i class="fas fa-sync-alt nft-market__refresh-icon"></i> Refresh
            </button>
            <div v-if="wallet && connected" class="nft-market__tab-buttons">
              <button 
                @click="activeTab = 'all'" 
                class="nft-market__tab-button"
                :class="{'nft-market__tab-button--active': activeTab === 'all'}"
              >
                All
              </button>
              <button 
                @click="activeTab = 'my'" 
                class="nft-market__tab-button"
                :class="{'nft-market__tab-button--active': activeTab === 'my'}"
              >
                My Listings
              </button>
            </div>
          </div>
        </div>
        
        <!-- Загрузка -->
        <div v-if="isLoading" class="nft-market__loader-container">
          <div class="nft-market__loader"></div>
        </div>
        
        <!-- Нет листингов -->
        <div v-else-if="displayedListings.length === 0" class="nft-market__empty-state">
          <p class="nft-market__empty-title">No NFT listings found.</p>
          <p v-if="activeTab === 'my'" class="nft-market__empty-subtitle">
            You haven't listed any NFTs for sale yet.
          </p>
          <p v-else class="nft-market__empty-subtitle">
            Be the first to list your NFT on our marketplace!
          </p>
        </div>
        
        <!-- Сетка листингов -->
        <div v-else class="nft-market__listings-grid">
          <NftListingCard 
            v-for="listing in displayedListings" 
            :key="listing.id" 
            :listing="listing"
            :wallet-address="walletAddress"
            :connected="connected"
            @view="viewListing"
            @bid="placeBid"
            @cancel="cancelListing"
          />
        </div>
      </div>
    </div>
    
    <!-- Модальные окна -->
    <CreateListingModal 
      v-if="showCreateListingModal" 
      :user-nfts="userNfts"
      :is-loading="isLoadingUserNfts"
      :is-submitting="isCreatingListing"
      @close="showCreateListingModal = false"
      @submit="handleCreateListing"
    />
    
    <PlaceBidModal 
      v-if="showBidModal && selectedListing" 
      :listing="selectedListing"
      :is-submitting="isPlacingBid"
      @close="showBidModal = false"
      @submit="handlePlaceBid"
    />
    
    <CancelListingModal 
      v-if="showCancelModal && selectedListing" 
      :is-submitting="isCancelling"
      @close="showCancelModal = false"
      @confirm="handleCancelListing"
    />
  </div>
</template>

<script>
import { ref, computed, onMounted, watch } from 'vue';
import { useWallet } from 'solana-wallets-vue';
import { useRouter } from 'vue-router';
import axios from 'axios';
import * as solanaWeb3 from '@solana/web3.js';

import NftListingCard from '@/components/nft-market/NftListingCard.vue';
import CreateListingModal from '@/components/nft-market/CreateListingModal.vue';
import PlaceBidModal from '@/components/nft-market/PlaceBidModal.vue';
import CancelListingModal from '@/components/nft-market/CancelListingModal.vue';

export default {
  name: 'NftMarketPage',
  components: {
    NftListingCard,
    CreateListingModal,
    PlaceBidModal,
    CancelListingModal
  },
  setup() {
    const { wallet, connected, publicKey } = useWallet();
    const router = useRouter();
    
    // Состояние данных
    const listings = ref([]);
    const myListings = ref([]);
    const userNfts = ref([]);
    const selectedListing = ref(null);
    
    // Состояние UI
    const isLoading = ref(false);
    const isLoadingUserNfts = ref(false);
    const isCreatingListing = ref(false);
    const isPlacingBid = ref(false);
    const isCancelling = ref(false);
    const activeTab = ref('all');
    const showCreateListingModal = ref(false);
    const showBidModal = ref(false);
    const showCancelModal = ref(false);
    
    // Вычисляемые свойства
    const walletAddress = computed(() => {
      return publicKey.value ? publicKey.value.toString() : '';
    });
    
    const displayedListings = computed(() => {
      return activeTab.value === 'my' ? myListings.value : listings.value;
    });
    
    // Методы для загрузки данных
    const loadListings = async () => {
      try {
        isLoading.value = true;
        const response = await axios.get('/nft-market/listings');
        listings.value = response.data.data;
      } catch (error) {
        console.error('Error loading listings:', error);
      } finally {
        isLoading.value = false;
      }
    };
    
    const loadMyListings = async () => {
      if (!connected.value) return;
      
      try {
        const response = await axios.get('/nft-market/listings/my');
        myListings.value = response.data.data;
      } catch (error) {
        console.error('Error loading my listings:', error);
      }
    };
    
    const loadUserNfts = async () => {
      if (!connected.value) return;
      
      try {
        isLoadingUserNfts.value = true;
        const response = await axios.get('/nft-market/my-nfts');
        userNfts.value = response.data.data;
      } catch (error) {
        console.error('Error loading user NFTs:', error);
        userNfts.value = [];
      } finally {
        isLoadingUserNfts.value = false;
      }
    };
    
    // Обработчики событий
    const viewListing = (listing) => {
      router.push(`/nft-market/listing/${listing.mintAddress}`);
    };
    
    const placeBid = (listing) => {
      selectedListing.value = listing;
      showBidModal.value = true;
    };
    
    const cancelListing = (listing) => {
      selectedListing.value = listing;
      showCancelModal.value = true;
    };
    
    const handleCreateListing = async (data) => {
      try {
        isCreatingListing.value = true;
        
        // Если данные пришли как событие, значит что-то пошло не так
        if (data.isTrusted) {
          // Используем последние выбранные данные
          const listingData = {
            nft: userNfts.value[0],
            initialPrice: 0.1,
            instantPrice: 0.2,
            endDate: new Date(Date.now() + 86400000).toISOString().slice(0, 16) // завтра
          };
          console.log('Using fallback NFT data:', listingData);
          data = listingData;
        }
        
        console.log('Creating listing for NFT:', JSON.stringify(data, null, 2));
        
        // Проверяем, что данные о NFT существуют и имеют mintAddress
        if (!data.nft || !data.nft.mintAddress) {
          console.error('Missing NFT data or mintAddress:', data);
          alert('Invalid NFT data. Please select an NFT and try again.');
          isCreatingListing.value = false;
          return;
        }
        
        // Шаг 1: Получаем инструкции для создания листинга
        console.log('Requesting listing instructions for:', data.nft.mintAddress);
        const instructionsResponse = await axios.post('/nft-market/listing-instructions', {
          mintAddress: data.nft.mintAddress,
          initialPrice: data.initialPrice
        });
        
        console.log('Instructions response:', JSON.stringify(instructionsResponse.data, null, 2));
        
        if (!instructionsResponse.data.success) {
          console.error('Error getting listing instructions:', instructionsResponse.data);
          alert('Failed to get listing instructions. Please try again.');
          isCreatingListing.value = false;
          return;
        }
        
        // Получаем данные инструкций из ответа сервера
        const instructions = instructionsResponse.data.data;
        console.log('Instructions data:', JSON.stringify(instructions, null, 2));
        
        // Проверяем, что получена сериализованная транзакция
        if (!instructions.serializedTransaction) {
          console.error('Missing serializedTransaction in instructions:', instructions);
          alert('Failed to get transaction data. Please try again.');
          isCreatingListing.value = false;
          return;
        }
        
        // Шаг 2: Десериализуем и подписываем транзакцию
        try {
          // Получаем провайдер Phantom
          const provider = window.solana;
          if (!provider) {
            throw new Error('Phantom wallet not found');
          }
          
          // Декодируем транзакцию из Base64
          const { Transaction } = solanaWeb3;
          const connection = new solanaWeb3.Connection('https://sleek-icy-frog.solana-mainnet.quiknode.pro/9d4845e2d4c60caf45dc4f10d0bdcfdbcd6a321c');
          
          // Десериализуем транзакцию
          console.log('Deserializing transaction...');
          const txBytes = Buffer.from(instructions.serializedTransaction, 'base64');
          const transaction = Transaction.from(txBytes);
          
          console.log('Transaction deserialized successfully:', {
            recentBlockhash: transaction.recentBlockhash,
            feePayer: transaction.feePayer.toString(),
            instructions: transaction.instructions.length
          });
          
          // Запрашиваем подпись пользователя через Phantom
          console.log('Requesting signature from Phantom...');
          const signedTransaction = await provider.signTransaction(transaction);
          
          console.log('Transaction signed, sending to network...');
          
          // Отправляем подписанную транзакцию
          const signature = await connection.sendRawTransaction(signedTransaction.serialize());
          
          console.log('Transaction sent with signature:', signature);
          
          // Ждем подтверждения транзакции
          console.log('Waiting for transaction confirmation...');
          const confirmationResponse = await connection.confirmTransaction(signature);
          
          console.log('Transaction confirmation response:', confirmationResponse);
          
          if (confirmationResponse.value && confirmationResponse.value.err) {
            throw new Error(`Transaction failed: ${JSON.stringify(confirmationResponse.value.err)}`);
          }
          
          console.log('Transaction confirmed:', signature);
          
          // Шаг 3: Создаем листинг на сервере после успешного подписания транзакции
          console.log('Creating listing in database...');
          console.log('Data for createListing:', {
            mintAddress: data.nft.mintAddress,
            initialPrice: data.initialPrice,
            instantPrice: data.instantPrice,
            endTime: new Date(data.endDate).toISOString(),
            transactionSignature: signature
          });
          
          const createResponse = await axios.post('/nft-market/listings', {
            mintAddress: data.nft.mintAddress,
            initialPrice: data.initialPrice,
            instantPrice: data.instantPrice,
            endTime: new Date(data.endDate).toISOString(),
            transactionSignature: signature
          });
          
          if (createResponse.data.success) {
            console.log('Listing created successfully:', createResponse.data);
            
            // Закрываем модальное окно и обновляем списки
            showCreateListingModal.value = false;
            await loadListings();
            await loadMyListings();
          } else {
            console.error('Error creating listing:', createResponse.data);
            alert('Failed to create listing. Please try again.');
          }
        } catch (signError) {
          console.error('Error signing or sending transaction:', signError);
          alert('Failed to sign transaction. Please try again: ' + (signError.message || 'Unknown error'));
        }
      } catch (error) {
        console.error('Error creating listing:', error);
        alert('Failed to create listing. Please try again: ' + (error.message || 'Unknown error'));
      } finally {
        isCreatingListing.value = false;
      }
    };
    
    const handlePlaceBid = async (data) => {
      try {
        isPlacingBid.value = true;
        
        // Размещаем ставку на сервере
        const response = await axios.post('/nft-market/bid', {
          mintAddress: data.mintAddress,
          bidAmount: data.amount
        });
        
        // В реальной реализации здесь будет подписание транзакции
        // Для примера просто закрываем модальное окно и обновляем списки
        
        showBidModal.value = false;
        selectedListing.value = null;
        await loadListings();
        await loadMyListings();
        
      } catch (error) {
        console.error('Error placing bid:', error);
        alert('Failed to place bid. Please try again.');
      } finally {
        isPlacingBid.value = false;
      }
    };
    
    const handleCancelListing = async () => {
      try {
        isCancelling.value = true;
        
        // Отменяем листинг на сервере
        const response = await axios.post('/nft-market/listings/cancel', {
          mintAddress: selectedListing.value.mintAddress
        });
        
        // В реальной реализации здесь будет подписание транзакции
        // Для примера просто закрываем модальное окно и обновляем списки
        
        showCancelModal.value = false;
        selectedListing.value = null;
        await loadListings();
        await loadMyListings();
        
      } catch (error) {
        console.error('Error cancelling listing:', error);
        alert('Failed to cancel listing. Please try again.');
      } finally {
        isCancelling.value = false;
      }
    };
    
    // Жизненный цикл
    onMounted(async () => {
      await loadListings();
    });
    
    watch(connected, async (newValue) => {
      if (newValue) {
        await loadMyListings();
      } else {
        myListings.value = [];
        activeTab.value = 'all';
      }
    });
    
    watch(showCreateListingModal, async (newValue) => {
      if (newValue) {
        await loadUserNfts();
      }
    });
    
    return {
      wallet,
      connected,
      walletAddress,
      listings,
      myListings,
      userNfts,
      selectedListing,
      isLoading,
      isLoadingUserNfts,
      isCreatingListing,
      isPlacingBid,
      isCancelling,
      activeTab,
      showCreateListingModal,
      showBidModal,
      showCancelModal,
      displayedListings,
      loadListings,
      loadMyListings,
      loadUserNfts,
      viewListing,
      placeBid,
      cancelListing,
      handleCreateListing,
      handlePlaceBid,
      handleCancelListing
    };
  }
};
</script>

